.filters{
    padding:16px 0px;
    display:flex;
    flex-wrap: wrap;
}
.filtersr{
    margin:.5em;
    padding:7px 9px;
    border:1px solid rgb(154, 150, 150);
    border-radius: 9px;
    color:rgb(156, 156, 156);
    cursor: pointer;
    display:flex;
    align-items: center;
    .icon{
        position: relative;
        top:.2em;
    }
}