.footer-flex{
    display:flex;
    justify-content:space-between;
    flex-wrap: wrap;
}
.footer{
    background-color:rgb(245, 238, 238);
    padding-top:4em;
padding-bottom: 3em;
}
.footerlist{
    display:flex;
    flex-direction:column;
    gap:.7em;
    h3{
        font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }
}
.footer-item{
    color:rgb(65, 62, 62);
    cursor: pointer;
}