.logo img{
    width:100%;
    height: 25px;
}

hr{
    width:2em;
    transform: rotate(90deg);
}
.boxheader{
    margin:1em 1em;
    padding:.6em .5em;
    box-shadow:rgba(28, 28, 28, 0.08) 0px 2px 8px;
    border:1px solid rgb(232, 232, 232);
    border-radius: 0.8em;
}
.option:active{
    transform: rotate(180deg);
}
.login{
    color: rgb(128, 123, 123);
    font-size: 1.5em;
    margin:0px .4em;
}
li{
    list-style-type: none;
    margin:0px 20px;
    font-size:1.6rem;
    font-weight: 350;
    color:rgb(105, 105, 105);
}
li:hover{
    cursor: pointer;
    color:red;
}
.taboptioni{
    height:35px;
}
.tabib{
    padding:10px 15px;
    border-radius:70%
}
.colors{
    background-color:rgb(248, 248, 248);
}
.scooterc{
    background-color:rgb(252, 238, 192);
}
.toboption{
    transform: rotate(0deg);
    width:100%;
    margin:1.5em 0em .6em 0em;
}
.collection-title-description{
 color:black;
 font-size: large;
 font-family: Arial, Helvetica, sans-serif;
}
