.explore-card{
    margin:12px 0px 24px 0px;
    padding:10px;
    border-radius:15px;
    border:1px solid transparent;
}
.explore-card-cover{
    height:248px;
    width: 328px;
    border-radius: 15px;
    position: relative;
}
.explore-card-image{
    height:200px;
    width:300px;
    object-fit:cover;
    border-radius:15px;
}
.delivery-time{
   position: relative;
   top:-2em;
   background-color: white;
   width: 3em;
   backdrop-filter: blur(4px);
   opacity: 0.8;
   border-radius: 0px 8px 0px 0px
}
.pro-off{
    position: absolute;
    left:0px;
    bottom:40px;
    background-color:#d92662;
    color:white;
    font-size: 14px;
    padding :0px 6px;
    border-radius: 0px 2px 2px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}
.discount{
    position: relative;
    top:-15em;
    width: 9em;
    background-color:#256fef;
    color:white;
    font-size: 14px;
    padding :0px 6px;
    border-radius: 0px 7px 10px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}
.gold-off{
    position: absolute;
    left:0px;
    bottom:14px;
    background-color:#d92662;
    color:white;
    font-size: 14px;
    padding :0px 6px;
    border-radius: 0px 2px 2px 0px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 18px;
    font-weight: 500;
}
.res-row{
    display:flex;
    justify-content: space-between;
    padding:0px 0px 6px;
}
.res-name{
    max-width:200px;
    overflow:hidden;
    text-overflow: ellipsis;
    font-size: 18px;
    font-weight: 600;
}
.res-rating{
    padding:0px 8px;
    font-size: 13px;
    font-weight: 600;
    color:white;
    background-color:green;
    border-radius: 6px;
    height: 24px;
    width: 24px;
}
.res-rating i{
  font-size: 10px;
  margin-left: 2px;
}
.res-cuisine{
    max-width: 56%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size:14px ;
}
.res-cuisine-tag{
    margin-right:4px;
    color:rgb(60, 57, 57);
}
.res-price{
    font-size: 14px;
}
.explore-bottom{
    display: flex;
    align-items: center;
    max-width: 100%;
}
.res-bottom-text{
    font-size: 12px;
    color:rgb(60, 57, 57);
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipse;
    margin-left: 8px;
}
.card-separator{
    height:1px;
    margin:10px 0px 5px;
    background:rgb(159, 157, 157);
}
.explore-card:hover{
    background-color: #fff;
    border:1px solid rgb(188, 182, 182);
    box-shadow:0px 3px 3px 3px rgb(188, 182, 182) ;
}
.icondinig{
    display:flex;
   align-items: center;
   gap:1em;
   position: relative;
   top:-3em;
   width: 8em;
   padding: .2em .5em;
  color: rgb(140, 97, 21);
  background-color:black;
  font-weight: bold;
 backdrop-filter: blur(15px);
  border-radius: 0px 12px 0px 0px;
}